.cSocialMediaLink_Wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cSocialMediaLink_Wrapper a {
  margin: 0;
  display: block;
  margin: 0 0.2em;
}

.cSocialMediaLink_Wrapper svg {
  display: block;
  width: 1.3em;
  fill: black;
}

.cSocialMediaLink_Wrapper .fb:hover {
  fill: #4267b2;
}

.cSocialMediaLink_Wrapper .nstgrm:hover {
  fill: url(#nstgrm_gradient);
}

