.stickyBar {
  position: fixed;
  top: 0;
  background-color: white;
}

nav {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 100%;
  padding: 0;
  margin: 0;
}

nav h3 {
  margin: 0;
  padding: 0;
}

nav .cNav_TopBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

nav .cNav_TopBar .cNav_Logo_Wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}

nav .cNav_TopBar .cNav_Logo_Wrapper .logoAsLink {
  margin: 0;
  margin-right: 0.2em;
  margin-left: 0.2em;
  padding: 0;
  display: block;
  height: 2.5em;
}

nav .cNav_TopBar .cNav_Logo_Wrapper .logoAsLink img {
  padding: 0;
  margin: 0;
  max-width: 2.5em;
  max-height: 2.5em;
}

nav .cNav_TopBar .cNav_Logo_Wrapper .logoAsLink img:hover {
  opacity: 0.75;
}

nav .cNav_TopBar .platformLinks ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-self: top;
}

nav .pageLinks_Wraper {
  margin: 0;
  width: 100%;
  background-color: rgba(255, 172, 172, 0.4);
}

nav .pageLinks_Wraper .pageLinks {
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

nav .pageLinks_Wraper .pageLinks li {
  border-left: 1px solid black;
  padding: 0 10px;
  margin: 0;
}

nav .pageLinks_Wraper .pageLinks li:first-child {
  margin-left: 1em;
}

nav .pageLinks_Wraper .pageLinks li:last-child {
  margin-right: 1em;
}

nav .pageLinks_Wraper .pageLinks li:hover {
  border-left: 5px solid #d484ea;
  padding-left: 4px;
}

nav .pageLinks_Wraper a {
  text-decoration: none;
  color: black;
  font-size: 1em;
  font-weight: bold;
  margin: 0;
  padding: 0;
  display: block;
}

nav .pageLinks_Wraper a:hover {
  color: #d484ea;
  cursor: pointer;
}

@media (max-width: 600px) {
  nav .pageLinks_Wraper {
    position: fixed;
    bottom: 0;
  }
}
/*# sourceMappingURL=navBar.css.map */
.cAbout_MainWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f2c3f9;
}

.cAbout_MainWrapper .title {
  margin: 20px auto;
  font-family: "Clicker Script", cursive;
}

.cAbout_MainWrapper .photoBox {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 20px auto;
  padding: 0 20px;
  max-width: 90%;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 50px;
}

.cAbout_MainWrapper .photoBox > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cAbout_MainWrapper .photoBox .descriptionBox {
  max-width: 15em;
  max-height: 12em;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.cAbout_MainWrapper .photoBox .photo {
  max-width: 300px;
  max-height: 400px;
  margin: 15px;
}

@media (min-width: 600px) {
  .cAbout_MainWrapper .photoBox {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .cAbout_MainWrapper .photoBox .descriptionBox {
    align-self: flex-end;
  }
}

.cAbout_MainWrapper .subtitle {
  margin: 0.2em auto;
}

.cAbout_MainWrapper p {
  text-align: justify;
}
