.cDE_Square img {
  border-radius: 20px;
  margin: 0.5em;
}

.cDE_Square:hover {
  opacity: 0.8;
}

.cDE_Square img {
  width: 30vw;
  height: 30vw;
}

.cDE_Rectangle img {
  border-radius: 20px;
  margin: 0.5em;
}

.cDE_Rectangle:hover {
  opacity: 0.8;
}

.cDE_Rectangle img {
  width: 80vw;
  height: 45vh;
}

.cDE_Cover img {
  border-radius: 20px;
  margin: 0.5em;
}

.cDE_Cover:hover {
  opacity: 0.8;
}

.cDE_Cover img {
  border-radius: 0;
  width: 100vw;
  height: 50vh;
}

@media (orientation: portrait) {
  .cDE_Cover img {
    width: 100wv;
    height: 25vh;
  }
  .cDE_Rectangle img {
    width: 80wv;
    height: 20vh;
  }
}
